<template>
  <div class="col-5">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">互联网舆情热词总览</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <svg :width="width" :height="height">
          <a href="javascript:void(0)" v-for="(tag,index) in tags" :key="index">
            <text :x="tag.x" :y="tag.y" :font-size="tag.w" :fill-opacity="((400+tag.z)/600)" :fill="tag.c">
              {{ tag.text }}
            </text>
          </a>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      width: 425, // svg宽度
      height: 300, // svg高度
      tagsNum: 20, // 标签数量
      RADIUS: 140, // 球的半径
      speedX: Math.PI / 360, // 球一帧绕x轴旋转的角度
      speedY: Math.PI / 360, // 球-帧绕y轴旋转的角度
      tags: [],
      timer: {}
    }
  },
  computed: {
    CX () {
      // 球心x坐标
      return this.width / 2
    },
    CY () {
      // 球心y坐标
      return this.height / 2
    }
  },
  mounted () {
    this.init()
    this.timer = setInterval(() => {
      this.rotateX(this.speedX)
      this.rotateY(this.speedY)
    }, 17)
  },
  destroyed () {
    clearInterval(this.timer)
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_SENTIMENT_ANALYSIS',
        params: {},
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    createRandomItemStyle () {
      return {
        normal: {
          color: function color () {
            return (
              'rgb(' + [
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160)
              ].join(',') +
                ')'
            )
          }
        }
      }
    },
    rotateX (angleX) {
      var cos = Math.cos(angleX)
      var sin = Math.sin(angleX)
      for (let tag of this.tags) {
        var y1 = (tag.y - this.CY) * cos - tag.z * sin + this.CY
        var z1 = tag.z * cos + (tag.y - this.CY) * sin
        tag.y = y1
        tag.z = z1
      }
    },
    rotateY (angleY) {
      var cos = Math.cos(angleY)
      var sin = Math.sin(angleY)
      for (let tag of this.tags) {
        var x1 = (tag.x - this.CX) * cos - tag.z * sin + this.CX
        var z1 = tag.z * cos + (tag.x - this.CX) * sin
        tag.x = x1
        tag.z = z1
      }
    },
    draw (data) {
      let tags = []
      for (let i = 0; i < data.length; i++) {
        let tag = {}
        let k = -1 + (2 * (i + 1) - 1) / data.length
        let a = Math.acos(k)
        let b = a * Math.sqrt(data.length * Math.PI) // 计算标签相对于球心的角度
        tag.text = data[i].name
        tag.x = this.CX + this.RADIUS * Math.sin(a) * Math.cos(b) // 根据标签角度求出标签的x,y,z坐标
        tag.y = this.CY + this.RADIUS * Math.sin(a) * Math.sin(b)
        tag.z = this.RADIUS * Math.cos(a)
        tag.w = this.getFont(i, data)
        tag.c = (function color () {
          return (
            'rgb(' + [
              Math.round(Math.random() * 160),
              Math.round(Math.random() * 160),
              Math.round(Math.random() * 160)
            ].join(',') +
              ')'
          )
        })()
        tags.push(tag)
      }
      this.tags = tags
    },
    getMax (item) {
      let max = item[0]
      item.map(o => {
        if (o.value > max.value) max = o
      })
      return max
    },
    getMin (item) {
      let min = item[0]
      item.map(o => {
        if (o.value < min.value) min = o
      })
      return min
    },
    getScale (item) {
      let max = this.getMax(item)
      let min = this.getMin(item)
      let scale = (max.value - min.value) / 38
      return scale
    },
    getFont (i, item) {
      let scale = this.getScale(item)
      let max = this.getMax(item)
      let min = this.getMin(item)
      if (item[i].value === min.value) return 12
      if (item[i].value === max.value) return 50
      let font = Math.ceil((item[i].value - min.value) / scale) + 12
      return font
    }
  }
}

</script>



