<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title"><a href="javascript:void(0)" @click="$router.push('sentiment')">+更多</a>最新舆情</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="tablescoll">
          <el-row>
            <el-table :data='item' stripe height="260" ref='table' style='width: 100%' class="table table-striped">
              <el-table-column prop='title' min-width="200" show-overflow-tooltip label='标题' fixed>
              </el-table-column>
              <el-table-column width="100" show-overflow-tooltip label='来源'>
                <template slot-scope="scope">
                  {{scope.row.source}}
                </template>
              </el-table-column>
              <el-table-column prop='time' width="150" show-overflow-tooltip label='时间' fixed="right">
              </el-table-column>
            </el-table>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      item: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_LAST_SENTIMENT',
        params: {},
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.item = res
        }
      })
    }
  }
}

</script>

<style scoped>
  .el-table--fit {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }

</style>



