<template>
  <div class="col-7" style=" float:right; padding-right:0; " v-loading="loading">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">互联网数据区域分布</div>
        <a href="javascript:void(0)" @click="showPage=2,init()">
          <el-tag class="title-tab" :class="{'active': showPage==2}">抽检公告</el-tag>
        </a>
        <a href="javascript:void(0)" @click="showPage=1,init()">
          <el-tag class="title-tab" :class="{'active': showPage==1}">舆情</el-tag>
        </a>
      </div>
      <div class="panel-body" style="min-height:664px; overflow: auto;" id="chart5">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      showPage: 1
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_PROVINCE_ANALYSIS',
        params: {
          type: this.showPage
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let self = this
      let myChart = this.$echarts.init(document.getElementById('chart5'), 'macarons')
      let mapName = 'china'
      let geoCoordMap = {}
      let toolTipData = []

      /* 获取地图数据 */
      myChart.showLoading()
      let mapFeatures = this.$echarts.getMap(mapName).geoJson.features
      myChart.hideLoading()
      mapFeatures.forEach(function (v) {
        // 地区名称
        let name = v.properties.name
        // 地区经纬度
        geoCoordMap[name] = v.properties.cp
      })

      let max = 480
      let min = 9 // todo
      let maxSize4Pin = 100
      let minSize4Pin = 20

      let convertData = function (res) {
        let arr = []
        for (let i = 0; i < res.length; i++) {
          let geoCoord = geoCoordMap[res[i].name]
          if (geoCoord) {
            arr.push({
              name: res[i].name,
              value: geoCoord.concat(res[i].value)
            })
          }
        }
        return arr
      }
      let removeDirtyData = function (res) {
        let arr = []
        for (let i = 0; i < res.length; i++) {
          let geoCoord = geoCoordMap[res[i].name]
          if (geoCoord) {
            arr.push({
              name: res[i].name,
              value: res[i].value
            })
          }
        }
        return arr
      }
      let getMax = function (res) {
        let max = res[0].value
        for (let i = 0; i < res.length; i++) {
          if (res[i].value > max) max = res[i].value
        }
        return max
      }
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            if (typeof (params.value)[2] === 'undefined') {
              let toolTiphtml = ''
              for (let i = 0; i < toolTipData.length; i++) {
                if (params.name === toolTipData[i].name) {
                  toolTiphtml += toolTipData[i].name + ':<br>'
                  for (let j = 0; j < toolTipData[i].value.length; j++) {
                    toolTiphtml += toolTipData[i].value[j].name + ':' + toolTipData[i].value[j].value + '<br>'
                  }
                }
              }
              return toolTiphtml
            } else {
              let toolTiphtml = ''
              for (let i = 0; i < toolTipData.length; i++) {
                if (params.name === toolTipData[i].name) {
                  toolTiphtml += toolTipData[i].name + ':<br>'
                  for (let j = 0; j < toolTipData[i].value.length; j++) {
                    toolTiphtml += toolTipData[i].value[j].name + ':' + toolTipData[i].value[j].value + '<br>'
                  }
                }
              }
              return toolTiphtml
            }
          }
        },
        visualMap: {
          show: true,
          min: 0,
          max: getMax(removeDirtyData(a)),
          left: 'left',
          top: 'bottom',
          text: ['高', '低'], // 文本，默认为数值文本
          calculable: true,
          seriesIndex: [1],
          inRange: {
            // color: ['#3B5077', '#031525'] // 蓝黑
            // color: ['#ffc0cb', '#800080'] // 红紫
            // color: ['#3C3B3F', '#605C3C'] // 黑绿
            // color: ['#0f0c29', '#302b63', '#24243e'] // 黑紫黑
            // color: ['#23074d', '#cc5333'] // 紫红
            color: ['#00467F', '#A5CC82'] // 蓝绿
            // color: ['#1488CC', '#2B32B2'] // 浅蓝
          }
        },
        geo: {
          show: true,
          map: mapName,
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: false
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#031525',
              borderColor: '#3B5077'
            },
            emphasis: {
              areaColor: '#2B91B7'
            }
          }
        },
        series: [{
          name: '散点',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: convertData(a),
          symbolSize: function (val) {
            let result = val[2] / 10
            return result > 20 ? 20 : result
          },
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: true
            },
            emphasis: {
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#05C3F9'
            }
          }
        },
        {
          type: 'map',
          map: mapName,
          geoIndex: 0,
          aspectScale: 0.75, // 长宽比
          showLegendSymbol: false, // 存在legend时显示
          label: {
            normal: {
              show: true
            },
            emphasis: {
              show: false,
              textStyle: {
                color: '#fff'
              }
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#031525',
              borderColor: '#3B5077'
            },
            emphasis: {
              areaColor: '#2B91B7'
            }
          },
          animation: false,
          data: a
        },
        {
          name: '点',
          type: 'scatter',
          coordinateSystem: 'geo',
          symbol: 'pin', // 气泡
          symbolSize: function (val) {
            let a = (maxSize4Pin - minSize4Pin) / (max - min)
            let b = minSize4Pin - a * min
            b = maxSize4Pin - a * max
            let result = a * val[2] + b
            return result > 50 ? 50 : result
          },
          label: {
            normal: {
              show: true,
              textStyle: {
                color: '#fff',
                fontSize: 9
              },
              formatter: function (params) {
                return params.value[2]
              }
            }
          },
          itemStyle: {
            normal: {
              color: '#F62157' // 标志颜色
            }
          },
          zlevel: 6,
          data: convertData(a)
        },
        {
          name: 'Top 5',
          type: 'effectScatter',
          coordinateSystem: 'geo',
          data: convertData(removeDirtyData(a).sort(function (a, b) {
            return b.value - a.value
          }).slice(0, 5)),
          symbolSize: function (val) {
            let result = val[2] / 10
            return result > 15 ? 15 : result
          },
          showEffectOn: 'render',
          rippleEffect: {
            brushType: 'stroke'
          },
          hoverAnimation: true,
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: 'yellow',
              shadowBlur: 10,
              shadowColor: 'yellow'
            }
          },
          zlevel: 1
        }

        ]
      }
      myChart.setOption(option)
      myChart.on('click', function (params) {
        if (params.data && params.data.name) {
          if (self.showPage === 1) {
            self.$router.push({path: '/sentiment', query: {keyword: params.data.name}})
          } else {
            self.$router.push({path: '/notice', query: {keyword: params.data.name}})
          }
        }
      })
    }
  }
}

</script>

<style>
  .title-tab {
    float: right;
    margin-top: -20px;
    margin-right: 5px;
  }

  .el-tag {
    padding: 0 5px;
    height: 21px;
    line-height: 20px;
    color: #909399;
    font-weight: 500;
  }

  .el-tag.active {
    color: #409EFF;
    border: 1px solid #409EFF;
  }

</style>



