<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">行业信息走势</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="chart" id="chart4" style=" min-height:260px; "></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_INDUSTRY_TREND',
        params: {},
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let b = this.dataFormat(a)
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: b.xAxis
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: a.date
        },
        yAxis: {
          type: 'value'
        },
        series: b.series
      }
      let myChart = this.$echarts.init(document.getElementById('chart4'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        series: [],
        xAxis: []
      }
      obj.xAxis = a.chart.category
      let aseri = []
      a.chart.series[0].data.map(o => {
        o.map((o2, i) => {
          if (!aseri[i]) aseri[i] = []
          aseri[i].push(o2)
        })
      })
      aseri.map((o, i) => {
        if (!obj.series[i]) obj.series[i] = {}
        obj.series[i] = {
          name: a.chart.category[i],
          data: o,
          type: 'line'
        }
      })
      return obj
    }
  }
}

</script>



